import React from "react"

const computePeriod = (initial, final) => {
  if (!final || final === "present") {
    return null
  }

  const period = final - initial
  const formattedPeriod = period > 1 ? `${period} year` : `${period || 1} years`
  return <span className="text-xs">({formattedPeriod})</span>
}

export const Paragraph = ({ children }) => (
  <p className="m-0 pl-6 py-3 text-balance">{children}</p>
)

export const Stack = ({ children }) => (
  <p className="pl-6 py-6 text-balance text-sm italic">
    <span className="font-semibold">Stack: </span>
    {children}
  </p>
)

export const DetailSection = ({ children }) => (
  <div className="pl-6 hidden print:!block">{children}</div>
)

export const ExperienceItem = ({
  companyName,
  initialYear,
  finalYear = "present",
  showDates = true,
  children,
}) => (
  <div className="pl-6 border-l">
    <h3 className="text-xl m-0">
      <a href="https://www.google.com" target="_blank">
        {companyName}
      </a>
    </h3>
    {showDates && (
      <span className="text-sm text-gray-500">
        {initialYear} to {finalYear} {computePeriod(initialYear, finalYear)}
      </span>
    )}
    {!children && (
      <Paragraph>
        Lorem ipsum dolor sit amet consectetur adipiscing elit. Quisque faucibus
        ex sapien vitae pellentesque sem placerat. In id cursus mi pretium
        tellus duis convallis. Tempus leo eu aenean sed diam urna tempor.
        Pulvinar vivamus fringilla lacus nec metus bibendum egestas. Iaculis
        massa nisl malesuada lacinia integer nunc posuere. Ut hendrerit semper
        vel class aptent taciti sociosqu. Ad litora torquent per conubia nostra
        inceptos himenaeos.
      </Paragraph>
    )}
    {children}
  </div>
)
